/* =============================
   Global Container
============================= */
.login-container {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
  background: #007b8f;
  font-family: Arial, sans-serif;
}

/* =============================
   Card Box
============================= */
.login-box {
  background: #fff;
  padding: 30px;
  width: 380px;
  border-radius: 12px;
  box-shadow: 0 6px 16px rgba(0, 0, 0, 0.2);
  text-align: left;
}

/* Title */
.login-box h2 {
  text-align: center;
  margin-bottom: 20px;
  color: #222;
  font-size: 20px;
}

/* Labels */
.login-box label {
  font-size: 14px;
  font-weight: 500;
  margin-bottom: 6px;
  display: block;
  color: #333;
}

/* Inputs */
.login-box input {
  width: 100%;
  padding: 10px;
  margin-bottom: 15px;
  border: 1px solid #ccc;
  border-radius: 6px;
  font-size: 14px;
  transition: 0.2s;
}
.login-box input:focus {
  border-color: #007b8f;
  outline: none;
  box-shadow: 0 0 4px rgba(0, 123, 143, 0.4);
}

/* =============================
   Tabs (Email / Google / Phone)
============================= */
.login-tabs {
  display: flex;
  justify-content: space-around;
  margin-bottom: 20px;
  border-bottom: 2px solid #eee;
}

.login-tabs button {
  flex: 1;
  padding: 10px;
  border: none;
  background: none;
  font-weight: 600;
  cursor: pointer;
  transition: 0.2s;
  border-radius: 6px 6px 0 0;
}

.login-tabs button:hover {
  background: #f1f1f1;
}

.login-tabs button.active {
  background: #007b8f;
  color: #fff;
}

/* =============================
   Base Button Styles
============================= */
button {
  width: 100%;
  padding: 10px;
  font-size: 14px;
  border: none;
  border-radius: 6px;
  cursor: pointer;
  margin-bottom: 12px;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 8px; /* space for icon */
  font-weight: 500;
  transition: 0.2s;
}

/* Primary (Sign In / Sign Up) */
.btn-primary {
  background: #007b8f;
  color: white;
}
.btn-primary:hover {
  background: #006678;
}

/* Google */
.btn-google {
  background: #db4437;
  color: white;
}
.btn-google:hover {
  background: #c1351d;
}

/* Phone */
.btn-warning {
  background: #f4b400;
  color: white;
}
.btn-warning:hover {
  background: #db9e00;
}

/* Success (OTP verify) */
.btn-success {
  background: #0f9d58;
  color: white;
}
.btn-success:hover {
  background: #0c7a43;
}

/* =============================
   Divider
============================= */
.divider {
  text-align: center;
  margin: 15px 0;
  color: #888;
  font-size: 14px;
  position: relative;
}
.divider::before,
.divider::after {
  content: "";
  position: absolute;
  top: 50%;
  width: 40%;
  height: 1px;
  background: #ddd;
}
.divider::before {
  left: 0;
}
.divider::after {
  right: 0;
}

/* =============================
   Links
============================= */
.login-links {
  text-align: center;
  margin-top: 15px;
}
.login-links a {
  color: #007b8f;
  text-decoration: none;
  font-size: 14px;
}
.login-links a:hover {
  text-decoration: underline;
}

/* =============================
   Icons inside buttons
============================= */
button img {
  width: 18px;
  height: 18px;
}
